.body {
    background: linear-gradient(152.97deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    backdrop-filter: blur(20px);
    width: 420px;
    height: 300px;
    display: block;
    padding: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
    user-select: none;
}

.title {
    color: white;
    margin-top: 5px;
    font-weight: 300;
    font-size: 25px;
    display: block;
}

.subtitle {
    color: white;
    margin-top: 8px;
    font-weight: 200;
    font-size: 23px;
    display: block;
}

.extramargin {
    margin-top: 30px;
}

.bottomtext {
    font-weight: 400;
    font-size: 23px;
    display: block;
    position: absolute;
    color: #00FF85;
    bottom: 20px;
}

.lock {
    mask: url(../images/lock.svg);
    mask-size: 100% 100%;
    background-color: white;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 35px;
    height: 35px;
}

.chainlogo {
    position: absolute;
    top: 10px;
    right: 60px;
    width: 30px;
    height: 35px;
    background-size: 100%;
}

.bsclogo{
    background-image: url(../images/blockchains/bsc.svg);
}

.polygonlogo{
    height: 26px;
    top: 13px;
    background-image: url(../images/blockchains/polygon.svg);
}