.title {
    font-size: 40px;
    color: white;
    display: block;
    z-index: 5;
}

.maincontainer {
    margin: 15%;
    margin-bottom: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.subtitle {
    margin-top: 15px;
    font-family: 22px;
    color: white;
    display: block;
    z-index: 5;
}

.auditcardcontainer {
    margin: 0 auto;
}


.ellipse1 {
    background: url(./images/Ellipse.svg);
    background-size: 100% 100%;
    transform: rotate(45deg);
    width: 250px;
    height: 250px;
    z-index: -1;
    position: relative;
    top: -230px;
    right: -240px;
}

@keyframes ani {
    0% {
        transform: rotate(350deg);
    }

    50% {
        transform: rotate(351.5deg);
    }

    100% {
        transform: rotate(350deg);
    }
}


.topauditcard {
    transform: rotate(350deg);
    animation: ani 5s infinite;
}

.toplinebreak {
    width: 0;
    height: 0;
}

.sectiontwo {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
}

.sectionparagraph {
    font-size: 22px;
    color: white;
    text-align: center;
}

.inputcontainer {
    max-width: 600px;
    width: 50%;
    position: relative;
}

.verifyinput {
    height: 50px;
    background: linear-gradient(152.97deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    border-radius: 16px;
    outline: none;
    border: 1px solid black;
    color: white;
    font-size: 20px;
    padding: 0 20px;
    width: 100%;
    box-sizing: border-box;
}

.searchicon {
    mask-image: url(./images/search.svg);
    background-color: white;
    mask-size: 100%;
    height: 30px;
    width: 30px;
    position: absolute;
    top: 10px;
    right: 15px;
    cursor: pointer;
}

.ellipse2 {
    background-image: url(./images/Ellipse2.svg);
    background-size: 100% 100%;
    transform: rotate(45deg);
    width: 250px;
    height: 250px;
    z-index: -1;
    position: absolute;
    right: 50px;
    top: 200px;
}

.openauditbutton {
    cursor: pointer;
    width: 420px;
    height: 60px;
    text-align: center;
    background: linear-gradient(152.97deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    color: white;
    line-height: 60px;
    font-size: 24px;
    backdrop-filter: blur(20px);
    border-radius: 16px;
    margin-top: 10px;
    background: linear-gradient(152.97deg, rgba(0, 209, 255, 0.2) 0%, rgba(255, 255, 255, 0.5) 100%);
}

.verifyauditcard {
    margin-top: 40px;
}

.sectionthree {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    margin-top: 150px;
}

.contactsection {
    display: flex;
    width: 300px;
    justify-content: space-between;
    margin-top: 40px;
}

.contactrow {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    text-decoration: none;
}

.contactrow p {
    color: white;
}

.contacticon {
    background-size: 100%;
    width: 50px;
    height: 50px;
}

.telegramicon {
    background-image: url(./images/contact/telegram.svg);
}

.emailicon {
    background-image: url(./images/contact/email.svg);
}

.signalicon {
    background-image: url(./images/contact/signal.svg);
}

.footer {
    width: 100%;
    height: 60px;
    border-top: 1px solid white;
    margin-top: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

@media only screen and (max-width: 1500px) {
    .maincontainer {
        padding: 5%;
        padding-top: 15%;
    }
}

@media only screen and (max-width: 1200px) {
    .toplinebreak {
        width: 100%;
        height: 150px;
    }

    .inputcontainer {
        width: 80%;
    }
}

@media only screen and (max-width: 550px) {
    .maincontainer {
        padding: 1%;
        padding-top: 1%;
    }
}